import React from 'react'

function topslider() {
    const styles = {
        width: "100%",
        height: "100%"        
    };
  
        return (           
            <section id="slider" className="hero odd featured slider-h-auto">
                <div className="swiper-container no-slider animation slider-h-100 swiper-container-initialized swiper-container-horizontal">
                    <div className="swiper-wrapper">
                        <div className="swiper-slide slide-center swiper-slide-active">
                            <div id="particles-1" className="particles full-image" data-particle="bubble" data-mask="70">
                                <canvas className="particles-js-canvas-el" width="1903" height="937" style={styles}></canvas>
                            </div>
                            <div className="slide-content row">
                                <div className="col-12 d-flex inner">
                                    <div >
                                        <h1 className="title effect-static-text aos-init aos-animate" data-aos="zoom-out-up" data-aos-delay="400">
                                        <span style={{fontWeight:"200"}}>Technology is best</span> when it brings people <span className="text-white">together</span>. <br></br> 
                                        </h1>
                                        <p className="text-center">Work with a focus on creativity, combining design and results.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>            
        )    
}



export default topslider;
